.cta-1 {
  font-size: 1.3rem;
}

.cta-1, .cta-2 {
  line-height: 1;
  letter-spacing: .02em;
}

.ttu {
  text-transform: uppercase;
}

.btn {
  cursor: pointer;
  border-radius: 5.6rem;
  color: currentColor;
}

