.btn--primary {
  background-color: #000;
  color: #eaeef4;
  padding: .8rem 1.6rem .7rem;
}

.btn--blur {
  -webkit-backdrop-filter: blur(1rem);
  backdrop-filter: blur(1rem);
  background-color: rgba(0,0,0,.4);
}

@media(min-width:1024px) {
  .btn--primary {
    padding: 1.2rem 1.6rem 1.1rem;
  }
}
@media(hover: hover) {
  .btn {
    transition: background-color .5s cubic-bezier(.75, .1, .25, 1), color .5s cubic-bezier(.75, .1, .25, 1);
  }
  .btn:hover.btn--blur, .btn:hover.btn--secondary {
    background-color: #000;
    color: #eaeef4;
  }
  .btn:hover {
    transition: none;
  }
}

a:active, a:hover, input, select, textarea {
  outline: 0;
}