.home-hero {
  position: relative;
  color: #eaeef4;
  padding-top: 5.3rem;
  padding-bottom: 1.6rem;
  min-height: 100svh;
  display: flex;
  background-color: #1e1e1e;
  overflow: hidden;
}

@media(min-width: 1024px) {
  .home-hero {
    padding-top: 6rem;
    padding-bottom: 2.4rem;
  }
}

.home-hero__asset {
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.home-hero__asset, .home-hero__asset-trigger {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.base-video {
  position: relative;
  display: flex;
}

.base-video--fit {
  width: 100%;
  height: 100%;
}

video {
  object-fit: contain;
  overflow-clip-margin: content-box;
  overflow: clip;
}

.base-video--fit video {
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
  object-fit: cover;
}

.base-video video {
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity .4s;
}

.base-video--loaded video {
  opacity: 1;
}

.base-video {
  position: relative;
  display: flex;
}