.underline-link {
  position: relative;
}

.underline-link:before {
  content: "";
  position: absolute;
  bottom: 0.1rem;
  left: 0;
  width: 100%;
  height: 0.1rem;
  background-color: currentColor;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.4s cubic-bezier(0.75, 0.1, 0.25, 1);
  transition-delay: 0s;
}

.is-visible .underline-link:before {
  transform: scaleX(1);
  transition-delay: var(--link-underline-delay);
}

@media (hover: hover) {
  .underline-link {
    text-shadow: 0 0.08em #eaeef4, 0.08em 0.08em #eaeef4, -0.08em 0.08em #eaeef4;
  }
  .modal .underline-link {
    text-shadow: 0 0.08em #1e1e1e, 0.08em 0.08em #1e1e1e, -0.08em 0.08em #1e1e1e;
  }
  .underline-link:hover:before {
    transform: scaleX(0);
    transform-origin: right;
    transition-delay: 0s;
  }
}
